import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { Post } from '../model/post';
import { map } from 'rxjs/operators';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class PostService {

  constructor(private http: HttpClient) { }

  public getPost(): Observable<Post[]> {
    return this.http.get(`${environment.endpoint}/v1/post`).pipe(
        map(response => response as Post[])
    );
  }

  public getInstagramPost(url: string): Observable<any> {
    return this.http.get(`https://graph.facebook.com/v9.0/instagram_oembed?url=${url}&access_token=258fa5c34fe254035e46fea6e53f7870`);
  }

  public getInstagramInfo(): Observable<any> {
    return this.http.get(`https://www.instagram.com/deysonestrad/?__a=1`);
  }
}
