import { Component, OnInit } from '@angular/core';
import { faArrowRight,
         faArrowDown,
         faGripLinesVertical,
         faMobileAlt,
         faAt,
         faHeart} from '@fortawesome/free-solid-svg-icons';

import { faFacebookMessenger,
         faLinkedin,
         faInstagram,
         faWhatsappSquare,
      } from '@fortawesome/free-brands-svg-icons';

import { ActivatedRoute } from '@angular/router';
import { HomeService } from '../../shared/service/home.service';
import { Email } from '../../shared/model/email';
import { EmailRespusta } from '../../shared/model/emailRespuesta';
import { messagesEng, messagesEsp } from 'src/app/feature/shared/config/messages';
import { Store } from '@ngrx/store';
import { AppState } from 'src/app/app.reducer';

import home from '@iconify/icons-mdi/home';
import { ToastrService } from 'ngx-toastr';
import { posts } from 'src/app/feature/shared/config/posts';
import { skills } from 'src/app/feature/shared/config/skills';
import { PostService } from 'src/app/feature/post/shared/service/post.service';
import { Post } from 'src/app/feature/post/shared/model/post';


declare var $: any;
declare var Swiper: any;

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss']
})
export class HomeComponent implements OnInit {

  faFacebookSquare = faFacebookMessenger;
  faLinkedin = faLinkedin;
  faInstagram = faInstagram;
  faArrowRight = faArrowRight;
  faArrowDown = faArrowDown;
  faGripLinesVertical = faGripLinesVertical;
  faWhatsappSquare = faWhatsappSquare;
  faMobileAlt = faMobileAlt;
  faAt = faAt;
  faHeart = faHeart;

  homeIcon = home;

  messages = messagesEng;

  email: Email = new Email();
  emailRespuesta: EmailRespusta = new EmailRespusta();

  skills = skills;

  posts: any = [];

  constructor(private route: ActivatedRoute,
              private homeService: HomeService,
              private readonly store: Store<AppState>,
              private readonly postService: PostService,
              private toastr: ToastrService) {

                    this.postService.getPost().subscribe( posts2 => {
                      this.posts = posts2;
                      $(document).ready(() => {

                        $("#skillsSlider").owlCarousel({

                          items: 3,
                          loop: true,
                          margin: 30,
                          autoplay: true,
                          slideTransition: 'linear',
                          autoplayTimeout: 3000,
                          autoplaySpeed: 5000,
                          autoplayHoverPause: true,
                          rewind: true,
                          responsive: {
                            0: {
                              items: 2
                            },
                            576: {
                              items: 2
                            },
                            768: {
                              items: 3
                            },
                            992: {
                              items: 4
                            }
                        }
                      });

                        $("#testimonial").owlCarousel({

                          rewind: true,
                          autoplay: true,
                          autoplayTimeout: 7000,
                          autoplayHoverPause: true,
                          items:3,
                          margin:30,
                          loop:true,
                          dots:true,
                          responsiveClass: true,
                          autoplaySpeed: 2000,
                          responsive: {
                              0: {
                                items: 1
                              },
                              576: {
                                items: 1
                              },
                              768: {
                                items: 2
                              },
                              992: {
                                items: 3
                              }
                          }
                      });
                
                        $('#news-slider').owlCarousel({
                          loop: true,
                          rewind: true,
                          autoplay: true,
                          autoplayTimeout: 5000,
                          autoplayHoverPause: true,
                          margin: 10,
                          responsiveClass: true,
                          autoplaySpeed: 2000,
                          responsive: {
                              0: {
                                items: 1
                              },
                              576: {
                                items: 2
                              },
                              768: {
                                items: 3
                              },
                              992: {
                                items: 4
                              }
                          }
                        });
                      });
                    },
                    error => {
                      console.log('Error consultando POST: ', error);
                    });

    this.store.select('language').subscribe( languaje => {

      if (languaje === 'ESP') {
        this.messages = messagesEsp;
      } else {
        this.messages = messagesEng;
      }

    });
  }

  ngOnInit(): void {

    this.route.queryParams.subscribe( params => {
      const modulo = 'modulo';
      if (params[modulo]) {
        this.scroll(params[modulo]);
      }
    });
  }

  scroll(element: string) {
    if (element !== 'index') {
      document.getElementById(element).scrollIntoView({ behavior: 'smooth'});
    }
  }

  enviarEmail() {

    this.emailRespuesta = new EmailRespusta();

    this.email.nombre = $('#nombre').val();
    this.email.destinatario = $('#destinatario').val();
    this.email.mensaje = $('#mensaje').val();


    this.emailRespuesta.codigo = 99;
    if (!this.email.nombre) {
      this.emailRespuesta.mensaje = 'El campo Nombre es requerido';
    } else if (!this.email.destinatario) {
      this.emailRespuesta.mensaje = 'El campo Email es requerido';
    } else if (!this.email.mensaje) {
      this.emailRespuesta.mensaje = 'El campo Mensaje es requerido';
    } else {
      this.homeService.enviarEmail(this.email).subscribe( (response: EmailRespusta) => {
        this.toastr.info('Pronto me pondré e contacto contigo', 'Gracias!');
      },
      error => {
        this.toastr.error('No es posible contactar en este momento, por favor enviar un correo manual a: deyson12@gmail.com', 'Error!');
      });
    }
  }
}
